.loader {
    float: right;
    margin: 15px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background: transparent;
    /*margin: 30px auto 0 auto;*/
    border: solid 2px #424242;
    border-top: solid 2px #1c89ff;
    border-radius: 50%;
    opacity: 0;
}

.check {
    width: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    -webkit-transform: translate3d(-4px, 50px, 0);
            transform: translate3d(-4px, 50px, 0);
    opacity: 0;
}

.check span:nth-child(1) {
    display: block;
    width: 10px;
    height: 2px;
    background-color: #1c89ff;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.check span:nth-child(2) {
    display: block;
    width: 20px;
    height: 2px;
    background-color: #1c89ff;
    -webkit-transform: rotate(-45deg) translate3d(14px, -4px, 0);
            transform: rotate(-45deg) translate3d(14px, -4px, 0);
    -webkit-transform-origin: 100%;
            transform-origin: 100%;
}

.loader.active {
    -webkit-animation: loading 1s ease-in-out;
            animation: loading 1s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.check.active {
    opacity: 1;
    -webkit-transform: translate3d(-4px, 4px, 0);
            transform: translate3d(-4px, 4px, 0);
    transition: all 0.5s cubic-bezier(0.49, 1.74, 0.38, 1.74);
    transition-delay: 0.2s;
}


@-webkit-keyframes loading {
    30% {
        opacity: 1;
    }
    85% {
        opacity: 1;
        -webkit-transform: rotate(1080deg);
                transform: rotate(1080deg);
        border-color: #262626;
    }
    100% {
        opacity: 1;
        -webkit-transform: rotate(1080deg);
                transform: rotate(1080deg);
        border-color: #1c89ff;
    }
}


@keyframes loading {
    30% {
        opacity: 1;
    }
    85% {
        opacity: 1;
        -webkit-transform: rotate(1080deg);
                transform: rotate(1080deg);
        border-color: #262626;
    }
    100% {
        opacity: 1;
        -webkit-transform: rotate(1080deg);
                transform: rotate(1080deg);
        border-color: #1c89ff;
    }
}
